.header{
    height: 10vh;
    background:transparent;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 50px;

    position: relative;
    z-index: 999;

  
    &__left{
   
      
        ul{
            list-style: none;
            display: flex;
            align-items: center;
            li{
                margin-right:35px;
                a{
                    text-decoration: none;
                    color:#fff;
                    font-size:1.2rem;
                    font-weight: 400;
                   
                }
            }
        }
  
    }
    &__mid{
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        a{
            text-decoration: none;
            color:#fff;
            font-size:2.5rem;
            text-align: center;
          
        }
    }
    &__right{
        display: flex;
        align-items: center;
        
        a{
            text-decoration: none;
            color:#fff;
            font-size:1.2rem;
            font-weight: 400;
            margin-right:40px;
        }
        & > .MuiSvgIcon-root{
            color:#fff;
            cursor: pointer;
        }
    }

    &__hambuger{
        display: none;
    }


  
}
.navHambuger{
    display: none;
}
@media (max-width:950px){
    .header{
        &__left{
            display:none;
        }
        &__hambuger{
            display: inline-block;
            background-color: #fff;
            width:40px;
            height: 2px;

            position: relative;
            transition: all .345s;
            &::before{
                content:'';
                background-color: #fff;
                width:40px;
                height: 2px;

                position: absolute;
                bottom:15px;

            }
        }
        &__hambugerWhite{
            display: inline-block;
            background-color: #111;
            width:40px;
            height: 2px;

            position: relative;
            transform: rotate(45deg);
            transition: all .345s;
            &::before{
                content:'';
                background-color: #111;
                width:40px;
                height: 2px;

                position: absolute;
                bottom:0px;
                transform: rotate(90deg);
            }
        }
 
        &__right{
            a{
                display: none;
            }
        }
        &__white{
      
            background-color: #fff;
            transition: all .3s;
            a{
                color:#111;
            }
            & > .MuiSvgIcon-root{
                color:#111;
                cursor: pointer;
            }
        }
    }
    .navHambuger{
        width:100vw;
        height: calc(90vh - 40px);
        background-color: #fff;
        display: inline-block;
  
      
        ul{
            list-style: none;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            
            position:absolute;
            top:50%;
            left:50%;
            transform:translate(-50%,-50%);
     
            li{
                padding:40px 0;
                animation: navHambuger 0.5s linear 0s;
                a{
                    text-decoration: none;
                    color:#111;
                    font-size:3rem;
                }
            }
        }
 
    }
}
@media screen and (max-width:450px) {
    .header{
        padding:20px 10px;
        &__hambugerWhite{
            width:20px;
            &::before{
                width:20px;
            }
        }
        &__hambuger{
            width:30px;
            &::before{
                width:30px;
            }
        }
        &__mid{
            a{
                font-size:1.8rem;
            }
        }
        &__right{
            & > .MuiSvgIcon-root{
                font-size:1.2rem;
            }
        }
    }
    .navHambuger{
        ul{
            li{
                padding:20px 0;
                a{
                    font-size:1.5rem;
                }
            }
        }
    }
}
@keyframes navHambuger {
        0%{
            transform: translateY(50px);
        }
        100%{
            transform: translate(0px);
        }
}