.dill{
    text-align: center;
    margin-top:100px;
    margin-bottom: 60px;
    h1{
        color:#111;
        font-size:3.5rem;

    }
    p{
        font-size:1.1rem;
        color:rgb(66, 66, 66);
        margin-top:30px;
        margin-bottom: 40px;
    }
    &__subscribe{
        border:none;
        padding:20px 30px;
   
        background-color: #df7c6d;
        color:#fff;
        font-size:1.1rem;
        font-weight: 500;
        cursor: pointer;
        margin-bottom: 100px;
        &:hover{
            opacity: .8;
        }

    }
    &__images{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        img{
            width:400px;
            height: 400px;
            object-fit: cover;
        }
    }
}
@media screen and (max-width:1275px) {
    .dill{
        &__images{
            img{
                width:300px;
                height: 300px;
            }
        }
    }
}
@media screen and (max-width:1000px) {
    .dill{
        &__images{
            img{
                width:250px;
                height: 250px;
            }
        }
    }
}
@media screen and (max-width:780px) {
    .dill{
        &__images{
            img{
                width:200px;
                height: 200px;
            }
        }
    }
}
@media screen and (max-width:640px) {
    .dill{
        &__images{
            img{
                width:150px;
                height: 150px;
            }
        }
    }
}
@media screen and (max-width:490px) {
    .dill{
        h1{
            font-size: 2.2rem;
        }
        p{
            font-size:1rem;
        }
        &__images{
            flex-direction: column;

            img{
                width:250px;
                height: 250px;
                margin:10px 0;
            }
        }
    }
}