.products{
    margin-top:100vh;
   
    text-align: center;
    h1{
        color:#111;
        font-weight: 500;
        font-size:3rem;
    }
    p{
        margin-top:20px;
        font-size:1.1rem;
        color:rgb(77, 77, 77);
    }
    &__image{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top:160px;
        a{
            img{
                width:100%;
                height:100%;
                object-fit: cover;
            }
        }
    }
    &__shopNow{
        border:none;
        padding:20px 30px;
        margin-top:100px;
        background-color: #df7c6d;
        color:#fff;
        font-size:1.1rem;
        font-weight: 500;
        cursor: pointer;
        margin-bottom: 100px;
        &:hover{
            opacity: .8;
        }
    }
    &__imageFull{
        width:100%;
        height:700px;
        object-fit: cover;
    }
}
@media screen and (max-width:565px) {
    .products{
        h1{
            font-size:2rem;
        }
        p{
            font-size:1rem;
        }
        &__imageFull{
            height: 500px;
        }
    }
}