.footer{
    background-color: #DF7C6D;
    color:#fff;
    padding:50px;
    &__top{
        display: flex;
        align-items:center;
        justify-content: space-between;
        margin-bottom:30px;
        h1{
            font-size:1.5rem;
            font-weight: 600;
        }
    }
    &__mid{
        p{
            font-size:1rem;
            margin:7px 0;
            letter-spacing: 1px;
        }
    }
    &__bot{
        margin-top:40px;
        letter-spacing: 1px;
        a{
            
            color:#fff;
        }
    }
}