.background{
    background-image: url('https://images.squarespace-cdn.com/content/v1/5ce82b63338ade0001773744/1560194358763-0FSEP70XI34IL8XXZEQQ/pickle-perfection-v1.jpg');

    background-color: red;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    height: 100vh;
    position: absolute;
    top:0;
    left:0;
    right:0;
    z-index: -1;
    &__content{
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        text-align: center;
        h1{
            font-size:5.2rem;
            color:#fff;
            font-weight: 900;
        }
        button{
            border:none;
            padding:20px 30px;
            color:#df7c6d;
            background-color: #fff;
            font-size:1.2rem;
            margin-top:40px;
            cursor: pointer;
            &:hover{
                background-color: rgba(255, 255, 255,0.6);
            }
            &:focus{
                outline: none;
            }
        }
    }
}
@media screen and (max-width:450px) {
    .background{
        &__content{
            h1{
                font-size:3.5rem;
            }
        }
    }
}